    .add-container>input[type='text'] {
width:100%;
    }
} */

.types-container {
    flex: 1 1 100%;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

.types-container input[type='radio'] {
    position: absolute;
    top: -9999px;
}

.media-btns>input[type='radio'] {
    position: absolute;
    top: -9999px;
}

.media-btns {
    position: absolute;
    opacity: 0;
